.menu {
  padding: 2rem;
  background-color: $color-white;
  position: relative;
  align-items: center;
}

.menu__list{
  border: 0.2rem solid $color-manatee;
  border-radius: 0.5rem;
}

.menu__link{
  display: flex;
  padding: 1rem;
  font-size: 2.0rem;
  color: $color-gumbo;
  text-decoration: none;
  border-bottom: 0.2rem solid $color-gumbo;
}

.menu__label {
  padding: 0 1rem;
}

.menu__go-to {
  background-color: $color-fiord;
  color: $color-white;
  position: fixed;
  bottom: 0;
  right: 0;
  font-size: 2rem;
  padding: 1rem;
  border-radius: 0.5rem;
  text-decoration: none;
}


